<template>
    <div v-for="task in tasks" v-bind:key="task.id">
        <Task @toggle-reminder="$emit('toggle-reminder', task.id)" @task-delete="$emit('delete-task', task.id)" :task="task"/>
    </div>
</template>

<script>
import Task from './Task'

export default {
    name: 'Tasks',
    props: {
        tasks: Array
    },
    components: {
        Task
    },
    emits: ['delete-task', 'toggle-reminder']
}
</script>
